import "./styles.css";
import { useState } from "react";

const SaramTitle = () => {
  return <h2>주 타이틀</h2>;
};

const SaramSubTitle = () => {
  return (
    <>
      <h3>보조 타이틀1</h3>
      <h3>보조 타이틀2</h3>
    </>
  );
};

const Menu = (props) => {
  return (
    <h3>
      {props.data} : {props.item}
    </h3>
  );
};

export default function App() {
  const [menuitems, setMenuItems] = useState([
    "Apple",
    "Banana",
    "Cherry",
    "Durian"
  ]);

  return (
    <div className="App">
      <SaramTitle />

      <div>
        <SaramSubTitle />
      </div>
    </div>
  );
}
